// Подключение своих SCSS файлов
@import "fonts";
.wrapper {
    max-width: 1300px;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 10px;
}
body {
    font-family: "Montserrat";
}
a {
    text-decoration: none;
    color: #000;
}
button {
    border: none;
    cursor: pointer;
}
.main__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    &_img {
        img {
            width: 100%;
        }
    }
    &_left {
        max-width: 610px;
        position: relative;
    }
    &_right {
        max-width: 670px;
        > div {
            box-sizing: border-box;
            padding: 0 70px;
        }
    }
    &_icon {
        position: absolute;
        left: 60px;
        top: 60px;
    }
    &_top {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin: 40px 0 0 0;
        * {
            margin: 0 20px;
        }
    }
    &_center {
        margin-top: 100px;
    }
    &_title {
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 500;
        font-size: 68px;
        line-height: 83px;
        color: #156963;
        margin: 0 0 24px 0;
    }
    &_subtitle {
        font-family: "Montserrat";
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 32px;
        color: #2c1500;
        margin: 0 0 100px 0;
        opacity: 0.8;
    }
    &_bottom {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding-right: 0 !important;
    }
    &_bigbutton {
        width: 300px;
        height: 160px;
        padding: 45px 40px;
        box-sizing: border-box;
        span {
            display: block;
            margin: 0 0 13px 0;
            font-family: "Montserrat";
            font-style: normal;
            font-weight: 500;
            font-size: 32px;
            line-height: 39px;
            color: #ffffff;
        }
    }
}
.arrow {
    font-weight: 500;
    font-size: 24px !important;
    line-height: 29px !important;
    position: relative;
    width: max-content;
    &_prev {
        color: #333333 !important;
        &::after {
            left: -40px;
            top: 13px;
            background: #333333;
        }
        &::before {
            left: -40px;
            top: 7px;
            border-top: 1px solid #333333;
            border-right: 1px solid #333333;
            transform: rotate(-135deg);
        }
    }
    &_next {
        color: #f2f2f2 !important;
        &::before {
            right: -40px;
            top: 7px;
            border-top: 1px solid #f2f2f2;
            border-right: 1px solid #f2f2f2;
            transform: rotate(45deg);
        }
        &::after {
            background: #f2f2f2;
            right: -40px;
            top: 13px;
        }
    }

    &::after {
        position: absolute;
        content: "";
        display: block;
        width: 24px;
        height: 2px;
    }
    &::before {
        position: absolute;
        content: "";
        display: block;
        width: 12px;
        height: 12px;
    }
}
.button {
    width: 110px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Nunito";
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    &-white {
        color: #156963;
        span {
            color: #156963;
        }
    }
    &-green {
        color: #fff;
        background: #156963;
    }
}
.container {
    max-width: 1168px;
    margin: 0 auto;
}
.info {
    &__blocks {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-top: 140px;
        &_text {
            font-family: "Montserrat";
            font-style: normal;
            font-weight: normal;
            font-size: 10px;
            line-height: 16px;
            color: #2c1500;
        }
        &_block {
            max-width: 270px;
            background: #ffffff;
            border: 1px solid #e0e0e0;
            box-sizing: border-box;
            padding: 20px 16px;
            margin: 10px;
        }

        &_top {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            min-height: 48px;
            img {
                margin-right: 20px;
            }
            &-text {
                font-family: "Montserrat";
                font-style: normal;
                font-weight: bold;
                font-size: 16px;
                line-height: 24px;
                color: #156963;
                max-width: 140px;
                margin: 0;
            }
        }
    }
}
.plants {
    margin-top: 80px;
    &__top {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        height: 30px;
        align-items: center;
        border-bottom: 1px solid #e0e0e0;
        padding-bottom: 20px;
        &_title {
            font-family: "Montserrat";
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;
            margin: 0;

            color: #000000;
        }
        &_select {
            justify-self: end;
            width: 130px;
            font-family: " Montserrat";
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 15px;
            color: #000000;
            height: 30px;
        }
    }
    &__blocks {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
        &_block {
            margin: 8px;
        }
        &_text {
            font-size: 16px;
            line-height: 20px;
            text-align: center;
            color: #000000;
        }
        &_price {
            font-weight: 600;
            font-size: 16px;
            line-height: 20px;
            text-align: center;
            color: #000000;
        }
    }
}
.buy {
    margin-top: 100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    img {
        width: 100%;
    }
    &__left {
        max-width: 600px;
        display: flex;
        flex-wrap: wrap;
        img {
            align-self: flex-end;
        }
        &_title {
            font-weight: bold;
            font-size: 40px;
            line-height: 56px;
            color: #2c1500;
            margin: 0 0;
        }
        &_text {
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 28px;
            color: #2c1500;
            opacity: 0.8;
            margin: 0 0 20px;
        }
    }
    &__right {
        max-width: 510px;
        margin-left: 30px;
    }
}
.favorites {
    margin-top: 80px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background: #f3f4f6;
    box-sizing: border-box;
    padding: 0 10px;
    img {
        width: 100%;
    }
    &__left {
        max-width: 585px;
        &_title {
            font-weight: bold;
            font-size: 40px;
            line-height: 56px;
            color: #2c1500;
        }
        &_button {
            width: 152px;
            height: 40px;
        }
    }
    &__right {
        max-width: 400px;
    }
}
ul {
    list-style: none;
    padding: 0;
}

@import "footer";
@import "adaptive";
