.footer {
    margin-top: 80px;
    padding-top: 60px;
    border-top: 1px solid rgba(21, 105, 99, 0.3);
    ul {
        li {
            margin: 14px 0;
            a {
                font-family: "Work Sans";
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 16px;
                color: #000000;
            }
        }
    }
    &__content {
        display: flex;
        flex-wrap: wrap;
        max-width: 975px;
        margin: 0 auto;
        &_1 {
            max-width: 282px;
            &-text {
                font-family: "Work Sans";
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 19px;
                color: #333333;
            }
            &-address {
                font-family: "Work Sans";
                font-style: normal;
                font-weight: normal;
                font-size: 13px;
                line-height: 20px;
                color: #4f4f4f;
            }
        }
        &_2 {
            max-width: 95px;
            margin: 0 80px;
            &_title {
                font-family: "Work Sans";
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 16px;
                margin: 0 0 20px;
                color: #000000;
            }
        }
        &_3 {
            max-width: 95px;
        }
        &_4 {
            max-width: 200px;
            margin: 0 0 0 80px;
            input {
                border: 1px solid #f2f2f2;
                box-sizing: border-box;
                border-radius: 2px;
                width: 234px;
                height: 32px;
                padding: 8px 13px;
                font-family: "Work Sans";
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 16px;
                color: #000000;
                opacity: 0.5;
                margin-bottom: 15px;
                &::placeholder {
                    font-family: "Work Sans";
                    font-style: normal;
                    font-weight: normal;
                    font-size: 12px;
                    line-height: 16px;
                    color: #000000;
                    opacity: 0.5;
                }
            }
        }
    }
}
