@media all and (min-width: 420px) and (max-width: 800px) {
}
@media all and (max-width: 600px) {
    .title {
        font-size: 40px;
    }

    .main__content_right {
        width: 100%;
        > div {
            padding: 0 10px;
        }
    }
    .buy__left_title {
        font-size: 30px;
        line-height: 35px;
        margin-bottom: 10px;
    }
    .favorites__left_title {
        font-size: 30px;
        line-height: 35px;
    }
}
@media all and (max-width: 500px) {
    .main__content_top {
        * {
            margin: 0 10px;
        }
    }
    .footer__content {
        justify-content: center;
        > div {
            margin-top: 20px;
        }

        &_2 {
            margin: 0 40px;
        }
        &_3 {
        }
        &_4 {
            margin: 0 0 0 40px;
        }
    }
}
